    computed: {
        filteredGroups() {
            if (!this.selectedParentGroup) return [];
            return this.groups.filter(group => group.parent_id === this.selectedParentGroup);
        },
    },
    methods: {
        selectChip(chipName) {
            this.selectedParentGroup = this.selectedParentGroup === chipName ? null : chipName;
            this.dialogGroup = this.selectedParentGroup !== null;
        },
        selectGroup(group) {
            this.selectedGroup = group.name;
            this.dialogGroup = false;
        },
        async onFinish() {

            if (!this.selectedGroup) {
                this.dialogMessage = "Hujachagua Kundi";
                this.hasErrors = true;
                return;
            }
            if (!this.gender) {
                this.dialogMessage = "Hujachagua Jinsia";
                this.hasErrors = true;
                return;
            }
            if (!this.age) {
                this.dialogMessage = "Hujajaza umri wako";
                this.hasErrors = true;
                return;
            }
            try {
                this.processing = true;
                // Here you would send form data to your backend or Firebase.
                await addDoc(collection(db, "applications"), {
                    firstName: this.firstName,
                    lastName: this.lastName,
                    phoneNumber: this.phoneNumber,
                    gender: this.gender,
                    age: this.age,
                    region: this.selectedRegion.name,
                    district: this.selectedDistrict ? this.selectedDistrict.name : "",
                    parentGroup: this.selectedParentGroup,
                    group: this.selectedGroup,
                    regDate: serverTimestamp()
                });
                this.processing = false;
                this.helloName = `${this.firstName} ${this.lastName}`;
                this.firstName = '';
                this.lastName = '';
                this.phoneNumber = '';
                this.gender = '';
                this.age = '';
                this.selectedParentGroup = '';
                this.selectedGroup = '';
                this.isRegistered = true;
                setTimeout(() => {
                    window.scrollTo({
                        top: 0,
                        behavior: 'smooth' // Optional: Adds a smooth scrolling effect
                    });
                }, 1000);
            } catch (e) {
                this.processing = false;
                console.error("Error adding document: ", e);
            }
        },
    },
    mounted() {
        this.selectedRegion = { id: 2, name: "Dar es Salaam" };
    },
    watch: {
        firstName(newValue, oldValue) {
            console.log(newValue + oldValue); // Log the new value of firstName

            // Ensure both firstName and lastName have values greater than length 0
            if (newValue.length > 0 && this.lastName.length > 0 && this.phoneNumber.length > 9) {
                this.isFormValid = true;
            } else {
                this.isFormValid = false;
            }
        },
        lastName(newValue, oldValue) {
